import moment from './moment'

const formatTime = (time: string) => moment(time).calendar(null, {
  sameDay: '[], HH:mm',
  nextDay: '[], HH:mm',
  nextWeek: '[  ], dddd HH:mm',
  lastDay: '[], HH:mm',
  lastWeek: '[  ], dddd HH:mm',
  sameElse: 'DD.MM.YYYY, HH:mm'
});

export default formatTime;
